/*  */
.icon-arrow {
    width: 24px;
    height: 24px;
    display: inline-block;
    background:url('../../../static/imgs/icon.png') no-repeat -352px -296px;
}
.ant-tree.ant-tree-show-line li:not(:last-child)::before{
    border-left:1px dashed #d6d6d6;
}
.ant-tree-switcher_open,.ant-tree-switcher_close{
    width:28px;
    height:28px;
}
.ant-tree-switcher_open .icon-arrow {
    background-position: -380px -298px;
}
.ant-tree-switcher_close .icon-arrow {
    background-position: -352px -298px;
}

.ant-tree-switcher-noop .icon-arrow{
    background-position: -426px -295px;
}
.ant-tree-checkbox-checked .ant-tree-checkbox-inner{
    background-color: #6595f2;
    border-color: #6595f2;
}
.ant-tree-checkbox-inner{
    border: 1px solid #d6d6d6;
}
/* filter(筛选) */
.expandAll,.closeAll{
    width: 20px;
    height: 20px;
    float: left;
    border: 1px solid #ddd;
    text-align: center;
    vertical-align: middle;
    line-height: 17px;
    margin-right:10px;cursor:pointer;color:#686868;background:#f3f5f7;
}    
.expandAll:hover,.closeAll:hover{
    border-color:#4c8bfd;
    color:#4c8bfd;
}
.tree-active {
    border-color:#4c8bfd;
    color:#4c8bfd;
}
.filter-show .ant-drawer > *{
    transition: transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1), box-shadow 0.3s cubic-bezier(0.7, 0.3, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.7, 0.3, 0.1, 1), -webkit-box-shadow 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
}
.filter-hide .ant-drawer-content-wrapper{
    transform: translateX(100%);
}
.filter-show .ant-drawer-content-wrapper {
    transform: translateX(1%);
}
.filter-hide {
    /* display:none; */
    right:-358px !important;
}
